.widget{
    height: 100px;
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 10px;

    -webkit-box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px rgba(201, 201, 201, 0.47);
    border-radius: 5px;

    .left, .right{
        display: flex;
        flex-direction: column;
        justify-content: space-around;

        .title{
            font-weight: bold;
            font-size: 14px;
            color: gray;
        }

        .counter{
            font-size: 12px;
            font-weight: 300;
        }

        .link{
            width: max-content;
            font-size: 12px;
            border-bottom: 1px solid gray;
        }
        
        .percentage{
            display: flex;
            align-items: center;
            font-size: 14px;

            &.positive{
                color: #70d2f0
            }
            &.negative{
                color: red;
            }
        }

        .icon{
            font-size: 18px;
            padding: 5px;
            background-color: #3f4041;
            border-radius: 5px;
            color: white;
            align-self: flex-end;
        }

    }
}